import React from 'react';
import Parachains from '../../data/parachains.jsx';
import { m , Porcent } from '../../data/helpers.jsx';
import Tvl from '../../data/tvl.jsx';
import Share from '../../data/share.jsx';
import Logo from '../../components/Logo';
import ProgressBar from '../../components/ProgressBar';
import Dotmarket from '../../components/Dotmarket';

export default function CrowdloanList(data) {

  const raising     = data?.raise
  const contrib     = data?.contributions
  const parachain   = Parachains.parachain;
  const tvl         = data?.tvl
  const marketshare = [{}];


  if(raising && contrib) {
    const market = raising.reduce(function(sum, current) {
      return parseInt(sum) + parseInt(current.raised);
    }, 0);
    const share = contrib.reduce(function(sum, current) {
      return parseInt(sum) + parseInt(current.total);
  }, 0)

    marketshare.market = market;
    marketshare.share  = share;
    marketshare.porcent = Porcent(marketshare.share/marketshare.market);
  }  



  return (
    <>
    <div>

    </div>
      <div className="flex">
        <div className="box tvl">
            <h3> Total Value Locked </h3>
            <Tvl tvl={tvl} />
        </div>

        <div className="box small" >
          <h3>Parallel Share of Total Market</h3>
          <Share market={marketshare} price={tvl}/>
        </div>  
      </div>

      <div className="box full">
        <h3>Crowdloan by Projects</h3>
        <div class="selectors">
          <p>
            <span><i className="gray"/> Total DOT Raised </span>
            <span><i className="blue"/> Raised on Parallel </span>
          </p>
        </div>
        { !raising ? 'Loading...' : 
          raising.sort((a, b) => b.raised - a.raised).filter(data => data.paraId !== "2017" && data.paraId !== "2003" ).map((index) => {
            
            const id = index.paraId,
                  r  = index.raised,
                  t  = raising[0].raised;
            return (

              <div key={id} id={id}>
                <div className="flex">
                  
                  {!parachain ? 'Loading...' : parachain.filter(i => i.paraid === id).map(icon => (
                    <Logo src={icon}/>
                  ))}    

                  {!contrib ? 'Loading...' : contrib.filter(g => g.paraId === id).map(graph => {
                     const gTotal = graph.total/r,
                           rt     = r/t;
                    return (
                    <>
                      <ProgressBar width={rt} bar={gTotal}/>
                      <div>
                        <p className="marketData"><strong> { m(r) } </strong> | <span> { Porcent(gTotal) + '% (' + m(graph.total) + ')' } </span>  </p>
                      </div>
                    </>
                  )})}
                  
                </div>
              </div>
              
            )
         } )
        }
      </div>

      <div className="flex">
        <div className="box auctions" >
          <h3>DOT Auctions <span>Total Supply: <strong>1,103,303,471 DOT</strong></span></h3>
          <Dotmarket raise={raising} marketShare={marketshare.market}/>
        </div>   
      </div>
  
    </>
  );
}