import React, { useEffect, useState } from 'react';
import Header         from './components/header'
import CrowdloanList  from './components/crowdloanList'
import './styles/styles.css'; 

function App() {
  const raiseApi          = "https://auction-service-prod.parallel.fi/crowdloan/polkadot"
  const contributionsApi  = "https://auction-service-prod.parallel.fi/crowdloan/polkadot/contributions"
  const tvlApi            = "https://auction-service-prod.parallel.fi/crowdloan/total-value"

  const [raised        , setRaised        ] = useState();
  const [contributions , setContributions ] = useState();
  const [tvl           , setTvl           ] = useState();


  const getRaised = async () => {
    const res = await fetch(raiseApi);
    const responseJson = await res.json();

    setRaised(responseJson)
  }

  const getContributions = async () => {
    const res = await fetch(contributionsApi);
    const responseJson = await res.json();

    setContributions(responseJson)

  }

const getTvl = async () => {
  const res = await fetch(tvlApi);
  const responseJson = await res.json();
  setTvl(responseJson)
}
  
  useEffect(()=>{
    function load() {
      getRaised();
      getContributions();
      getTvl()

    }

    load();


      setInterval(() => {
        load()
      }, 30000)
  
  }, [])

  return (
    <>
      <div className="container">
        <Header />
        <CrowdloanList raise={raised} contributions={contributions} tvl={tvl}/>
      </div>
    </>
  );
}

export default App;
